
.indicators {
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: calc(50% - 150px);

  display: none;

  width: 300px;
  margin-bottom: 10px;

  font-size: 12px;

  align-items: center;
  justify-content: center;

  span {
    display: flex;

    width: 24px;
    height: 24px;
    margin: 5px;

    cursor: pointer;
    text-align: center;

    color: #222222;
    border-radius: 12px;
    background-color: #ffffff;
    box-shadow: 0 0 15px #000000;

    align-items: center;
    justify-content: center;
  }

  &__item {
    &.active {
      color: #ffffff;
      background-color: #222222;
      box-shadow: 0 0 15px #ffffff;
    }
  }
}
